<template>
	<view class="box">
		<view class="tabs">
			<view class="search">
				<uni-icons type="search" color="#999999" size="24" @click="handleSearch"></uni-icons>
				<input type="text" v-model="keyword" class="inputs" placeholder="搜索" />
			</view>
			<view class="top-box">
				<swiper-tab :tabs="tabs" :current="currentTab" :activeColor="bgcolor" :barColor="bgcolor"
					:background="background" :color="bgcolor" :fontSize="28" @change="onTabCgnage" :overwidth="10">
				</swiper-tab>
				<!-- <swiper-tab :tabs="tabs" :current="currentTab" :background="background" :color="bgcolor"
					:activeColor="bgcolor" :barColor="bgcolor" :fontSize="28" @change="onTabCgnage" :tabFontSize="28">
				</swiper-tab> -->
			</view>
		</view>
		<!-- <view class="content"> -->
		<scroll-view scroll-y class="content" @scrolltolower="handleLower">
			<work-order v-for="(item,index) in workList" :key="index" :order="item" @onCancelOrder="handleCancelOrder"
				@onPayNow="handlePayNow" @onworkOrder="handleWorkOrder">
			</work-order>
			<!-- <work-order v-for="(item,index) in table.list" :key="index" :order="item" @onCancelOrder="handleCancelOrder"
				@onPayNow="handlePayNow" @onworkOrder="handleWorkOrder">
			</work-order> -->
			<!-- <view v-if="workList.length === 0" class="nono-list">
				————&nbsp&nbsp&nbsp&nbsp 暂无数据&nbsp&nbsp&nbsp&nbsp————
			</view> -->
			<!-- <list-status :status="table.status" /> -->
		</scroll-view>
		<!-- </view> -->
	</view>
</template>

<script>
	import SwiperTab from '@/frame/components/swiper-tab/index.vue'
	import WorkStatusEnum from '@/utils/enum.ts'
	import WorkOrder from './components/work-order/index.vue'
	import {
		PostPackageOrderListAjax,
		GetClosePackageOrderAjax
	} from '@/apis/api/modules/user-center'
	import {
		GetOrderPayAjax
	} from '@/apis/api/modules/buyland'
	// import PageList from '@/frame/utils/create-list'
	// const list = new PageList({ //最开始获取数据渲染页面
	// 	api: PostPackageOrderListAjax,
	// 	data: {
	// 		Status: -1,
	// 		token: true //每次获取判断一下token
	// 	},
	// 	filedMaps: {
	// 		list: 'List',
	// 		total: 'total'
	// 	}
	// })
	export default {
		components: {
			SwiperTab,
			WorkOrder
		},
		// mixins: [list],
		data() {
			return {
				bgcolor: '#fff',
				background: '#00D08F',
				currentTab: 0,
				tabs: [{
					text: '全部',
					// value: WorkStatusEnum.全部
				}, {
					text: '待付款',
					// value: WorkStatusEnum.待付款
				}, {
					text: '待劳作',
					// value: WorkStatusEnum.待劳作
				}, {
					text: '待评价',
					// value: WorkStatusEnum.待评价
				}, {
					text: '已完成',
					// value: WorkStatusEnum.已完成
				}],
				lower: true,
				keyword: null,
				status: -1,
				pageIndex: 1,
				workList: []
			}
		},
		onLoad(e) {
			// 获取列表数据
			// this.getPageList()
			// this.addParams({
			// 	Keyword: this.keyword || null,
			// 	Status: Number(e.index),
			// })
			// this.resetList()
			this.status = Number(e.index)
			this.currentTab = Number(Number(e.index) + 1)
		},
		onShow() {
			this.getDate()
		},
		methods: {
			handleSearch() {
				this.getDate()
			},
			onTabCgnage(e) {
				this.pageIndex = 1
				this.status = e - 1
				// this.addParams({
				// 	Keyword: this.keyword || null,
				// 	Status: this.status,
				// })
				// this.resetList()
				this.getDate()
			},
			async handleLower() {
				// if (!this.lower) {
				// 	return false
				// }
				this.pageIndex = this.pageIndex + 1

				const result = await PostPackageOrderListAjax({
					Keyword: this.keyword || null,
					Status: this.status,
					PageIndex: this.pageIndex,
					PageSize: 4,
					token: true
				})
				if (result.Result.List.length > 0) {
					this.lower = true
					this.workList.push(...result.Result.List)
					return false
				}

				this.lower = false
			},
			async getDate() {
				const result = await PostPackageOrderListAjax({
					Keyword: this.keyword || null,
					Status: this.status,
					PageIndex: this.pageIndex,
					PageSize: 4,
					token: true
				})
				if (result.Result.List.length === 0) {
					this.lower = true
				}
				// this.lower = false
				this.workList = result.Result.List
			},
			/**
			 * 查看劳作
			 */
			handleWorkOrder(orderId) {
				uni.navigateTo({
					url: `/pages/user-center/user-work/work-order/index?orderId=${orderId}`
				})
			},
			/**
			 * 取消订单
			 * @param {Object} OrderId
			 */
			async handleCancelOrder(OrderId) {
				const result = await GetClosePackageOrderAjax({
					OrderID: OrderId,
					token: true
				})
				uni.showToast({
					title: result.Result,
					icon: 'none'
				})
				this.pageIndex = 1
				this.getDate()
			},
			/**
			 * 支付
			 * @param {Object} orderId
			 */
			async handlePayNow(orderNo) {
				const payOrder = await GetOrderPayAjax({
					orderNo: orderNo,
					payType: 2,
					token: true
				})
				let that = this
				uni.requestPayment({
					"provider": 'wxpay',
					"signType": "RSA",
					"appId": payOrder.Result.appId,
					"nonceStr": payOrder.Result.nonceStr,
					"package": payOrder.Result.package,
					"partnerId": payOrder.Result.partnerId,
					"prepayId": payOrder.Result.prepayId,
					"timeStamp": payOrder.Result.timeStamp,
					"paySign": payOrder.Result.paySign,
					success: function(res) {
						uni.showToast({
							title: '支付成功！',
							icon: 'success'
						})
						that.getDate()
					},
					fail: function(err) {
						uni.showToast({
							title: '支付失败！',
							icon: 'error'
						})
					}
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.box {
		height: 100%;
		background: #F7F7F7;
		display: flex;
		flex-direction: column;
		overflow: hidden;
	}

	.nono-list {
		text-align: center;
		color: #c1c1ba;
		padding: 30rpx 0;
	}

	.tabs {
		padding: 16rpx 30rpx 0;
		background: #00D08F;

		.search {
			display: flex;
			justify-content: start;
			align-items: center;
			padding-left: 22rpx;
			background: #FFFFFF;
			border-radius: 34rpx;
			// margin-bottom: 26rpx;

			.inputs {
				padding-left: 24rpx;
			}
		}
	}

	.content {
		padding: 0 30rpx;
		flex: 1;
		overflow: auto;
		box-sizing: border-box;
	}

	.order-list-wrap {
		padding-bottom: 30upx;
	}
</style>
